<template>
  <div class="row"><!-- Circle Name -->
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
    <div class="mb-3 col-lg-4 col-md-12 col-sm-12">
      <label for="inputName" class="form-label">그룹 이름</label>
      <input type="text" class="form-control" id="inputName" maxlength="10" ref="circlename" v-model="state.name" autofocus>
      <div class="form-text" ref="notice">필수 (*)</div>
    </div>
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
  </div>
  <div class="row"><!-- MaxPeople, Password -->
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
    <div class="mb-3 col-lg-2 col-md-6 col-sm-12">
      <label for="inputNumber" class="form-label"><img alt="Vue logo" src="../assets/icones/people.jpg">&nbsp;&nbsp;최대 회원수</label>
      <div><input type="number" class="form-control" id="inputNumber" ref="maxNumber" v-model="state.maxNumber" min='1' max='3000'></div>
      <div class="form-text" ref="maxNotice">숫자만 가능</div>
    </div>
    <div class="mb-3 col-lg-2 col-md-6 col-sm-12">
      <label for="inputPassword" class="form-label">비밀번호</label>
      <input type="password" class="form-control" id="inputPassword" v-model="state.pass">
      <div class="form-text">옵션</div>
    </div>
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
  </div>
  <div class="row"><!--Content-->
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
    <div class="form-floating mb-3 col-lg-4 col-md-12 col-sm-12">
      <textarea class="form-control" placeholder="Leave a comment here" id="floatingTextarea2" v-model="state.content" style="height: 100px"></textarea>
      <label for="floatingTextarea2">그룹 설명</label>
    </div>
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
  </div>
  <div class="row" > <!-- GoalSelector, Buttons -->
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
    <div class="mb-3 col-lg-2 col-md-6 col-sm-12">
      <label for="inputName" class="form-label">나의 목표</label> <SelectGoalList @setInput="goalid" />
    </div>
    <div class=" item buttons mb-3 col-lg-2 col-md-6 col-sm-12">
      <button class=" btn btn-primary" @click.prevent="createCircleHandler">저장</button>
      <button class="btn btn-secondary" @click.prevent="cancel">취소</button>
    </div>
    <div class="col-lg-4 col-md-0 col-sm-0"></div>
  </div>
</template>
<script>
import { reactive, ref } from '@vue/reactivity'
import axios from 'axios'
import { useRouter } from 'vue-router';
import SelectGoalList from './SelectGoalList.vue';
export default {
  setup() {
    const router = useRouter();
    const state = reactive({
      goalid: "",
      name: "",
      content: "",
      maxNumber: "",
      pass: "",
      email: sessionStorage.getItem("email"),
      token: sessionStorage.getItem("TOKEN"),
    });
    const goalid = (data) => {
      console.log(data);
      state.goalid = data;
    }
    const circlename = ref(null);
    const frm = ref(null);
    let notice = ref(null);
    let maxNotice = ref(null);
    let maxNumber = ref(null);
    const createCircleHandler = async () => {
      if (circlename.value.value === "") {
        notice.value.innerHTML = "그룹 이름을 입력해 주세요";
        notice.value.style.color = "red";
        circlename.value.focus();
        return false;
      }
      else if (maxNumber.value.value == 0 && maxNumber.value.value != null) {
        maxNotice.value.innerHTML = "최대 회원수를 확인해 주세요";
        maxNotice.value.style.color = "red";
        return false;
      } else if (maxNumber.value.value <= -1) {
        maxNotice.value.innerHTML = "최대 회원수를 확인해 주세요";
        maxNotice.value.style.color = "red";
        return false;
      }
      console.log("createCircle launch");
      const url = "http://43.201.60.125/api/createCircle";
      const headers = {
        "Content-Type": "application/json",
        Authorization: state.token,
        "token": state.token };
      const body = {
        user: state.email,
        name: state.name,
        content: state.content,
        maxNum: state.maxNumber,
        pass: state.pass,
      };
      console.log("goalid" + state.goalid);
      console.log("body" + body.user);
      console.log("name" + body.name);
      console.log("content" + body.content);
      console.log("maxNum" + body.maxNum);
      console.log("pass" + body.pass);
      const response = await axios.post(url, body, { headers }).then((res)=>{
        console.log('the res :'+res.data);
        console.log('the res :'+res.data.cid);
        alert("그룹이 성공적으로 만들어 졌습니다")
        const wannaGoId = res.data.cid
        //!\\------------------
        //TODO: go the the link witht he id.
        // router.push({ name: "privateProfile" });
        router.push({name: 'mycircleread', params:{id:res.data}});
        // router.push({name: 'mycircleread', params:{id:10}});
        //!\\------------------
      }).catch((error)=>{
      console.log('the error :'+error);

      });
      
      
      
    };
    function cancel() {
      frm.value.reset();
      circlename.value.focus();
    }
    return { circlename, maxNotice, maxNumber, frm, notice, cancel, createCircleHandler, state, goalid };
  },
  components: { SelectGoalList }
}
</script>
<style scoped>
.btn {
  margin: 0.7em;
}
input:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
textarea:focus{
  box-shadow: 0px 6px 10px rgba(6, 90, 92, 0.25);
  transition: all 0.2s;
  border: 1px solid rgba(6,90,92,0.5);
}
img{
width: 1.5em;
}
</style>
