<template>
    <div>
        <el-select v-on:change="onChange" @change="onEmit" v-model="state.lists.goalid" clearable
            placeholder="내 목표리스트에서 선택" style="width:325px;">
            <el-option v-for="list in state.lists" :key="list" :label="list.name" :value="list.goalid"></el-option>
        </el-select>
    </div>
</template>
  
<script>
import axios from 'axios';
import { reactive, ref } from 'vue';

export default {
    methods: {
        onEmit() {
            this.$emit("setInput", this.state.lists.goalid)
        }
    },
    setup() {

        const state = reactive({
            uid: '',
            lists: '',
            email: sessionStorage.getItem('email'),
            token: sessionStorage.getItem('TOKEN'),
        });
        const uid = ref('')
        const url = "http://43.201.60.125/api/goalNameList"
        const headers = {
            "Content-Type": "application/json",
            "Authorization": state.token,
            "token": state.token,
        }
        const body = { email: state.email }
        axios.post(url, body, { headers }).then(function (res) {
            if (res.data != null) {
                state.lists = res.data
                console.log(state.lists[0])
                console.log('state.lists[0].goalid' + state.lists[0].goalid)
                console.log('state.lists[0].goalName' + state.lists[0].name)
            } else {
                alert('too bad. check something')
            }
        })

        const onChange = () => {
            console.log(state.lists.goalid)
        }
        return { state, uid, onChange }
    }
}
</script>
  